import React, { Component } from 'react';
import Emp from './Emp';
import List from './List'
class Emps extends Component{

  /* Part One Of Class*/ 
  constructor(){
    super();
    this.state={
      emps:List,
      counter:0,
      newarr:[],
    }
    this.clickHandler=this.clickHandler.bind(this);
    // this.showCards=this.showCards.bind(this);
  }

/* Part Two Of Class*/
  clickHandler(){
    if(this.state.counter<this.state.emps.length){
      this.state.newarr.push(this.state.emps[this.state.counter]);
    this.setState(prev=>{
     return{ counter:prev.counter+1}
    });
    this.newlist=this.state.newarr.map(em=>{
      return  <Emp email={em.email} name={em.name} phone={em.phone} imge={em.imge} key={em.id}></Emp>;
    });
    }
    
  }
  showCards(){
    let sh=List.map(e=>{
      return <Emp name={e.name} phone={e.phone} imge={e.imge} key={e.id} email={e.email}></Emp>;  
    });
    return sh;
    
  }
   
     /* Part Three Of Class*/
  render()
{
  return !this.state.emps.length ?  <h1 className="text-center text-white">Loading...</h1> :
  (

    <div className="row">
      <button className="btn  btn-lg btn-block" style={{marginBottom:"20px",backgroundColor:"#a6afc8",opacity:"0.5"}} onClick={this.clickHandler}>Click Here To Show More Employees </button>
    {this.newlist}
    {this.showCards()}
    </div>
)
}
 
    

}
export default Emps;