import React, { Component } from 'react';
import S1 from './S1'
class Emp extends Component{
    render(){
        
    return(
        <div className="col-lg-4 col-md-6 point " style={{marginBottom:"20px"}} >
           <a href={<S1></S1>} style={{textDecoration:"none",color:"inherit"}} > 
                <div className="card ">
                        <div className="card-header"><img src={this.props.imge} alt="gt" style={{width:"100%",maxHeight:"200px"}} className="rounded"></img></div>
                        <div className="card-body " style={{backgroundColor:"lightgrey"}}>
                            
                            <h5><i>Name</i> : {this.props.name} </h5>
                            <h6><i>Phone</i>: {this.props.phone} </h6>
                            <h6><i>Email</i>: {this.props.email} </h6>
                        </div>
                        <div className="card-footer  "><i class="fa fa-facebook-square " aria-hidden="true" style={{margin:"20px",fontSize:"40px"}}></i>
                        <i class="fa fa-instagram " aria-hidden="true" style={{margin:"20px",fontSize:"40px"}}></i>
                        <i class="fa fa-linkedin-square " aria-hidden="true" style={{margin:"20px",fontSize:"40px"}}></i>
                        <i class="fa fa-twitter " aria-hidden="true" style={{margin:"20px",fontSize:"40px"}}></i>
                        </div>
                </div>
           </a>
        </div>
        
    )
    }

}
export default Emp;